.klick-ats-info{
    padding-left: 5px;
}

/* Navigation */
.custom-nav{
    float: right;
    height: 28px;
    margin-top: 3px;
    cursor: default;
    color: rgb(85, 85, 85);
}
.custom-nav .tablenav-pages-navspan{
    height: auto;
    width: 29px;
    cursor: pointer;
}
.custom-nav .tablenav-pages-navspan:not(.disabled):hover{
    color: rgb(255, 255, 255);
    box-shadow: none;
    border-color: rgb(91, 157, 217);
    background: rgb(0, 160, 210);
    outline: 0px;
}
.custom-nav .tablenav-pages-navspan.disabled{
   cursor: default;
}

/* Advance search form */
.klick-logo-and-title{
    background: #fff;
    padding: 10px 30px;
}
.klick-logo-and-title img{
    width: 100%;
    max-width: 500px;
    display: inline-block;
    vertical-align: middle;
    height: auto;
}
.klick-logo-and-title h2{
    font-size: 14px;
    vertical-align: middle;
    display: inline-block;
    margin: 0;
}

.klick-ats-advanceform{
    display: inline-block;
    vertical-align: top;
    background: #063B6D;
    padding: 15px 20px;
    width: 100%;
    border-radius: 2px;
    position: relative;
    box-sizing: border-box;
}
.klick-ats-advanceform.disabled{
    opacity: 0.5;
}
.klick-ats-advanceform.disabled::before{
    content: '';
    background: rgba(0,0,0,0.5);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
.klick-ats-advanceform li{
    width: 14.28%;
    margin-bottom: 10px;
    float: left;
}
.klick-ats-advanceform li label{
    width: 100%;
    display: inline-block;
    font-weight: 600;
    padding-bottom: 3px;
    color: #FF9000;
    font-size: 12px;
}
.klick-ats-advanceform li span{
    color: #fff;
    font-size: 12px;
    margin-left: 45px;
}
.klick-ats-advanceform li select{
    width: 100%;
    max-width: 162px;
}
.klick-ats-advanceform li input[type="text"], .klick-ats-advanceform li input[type="date"], .klick-ats-advanceform li select{
    min-height: 30px;
    height: 30px;
    box-shadow: none;
    border: 0;
    border-radius: 2px;
    width: 160px;
    padding: 5px 10px;
}
.klick-ats-advanceform li textarea{
    height: 66px;
    resize: none;
}
.klick-ats-advanceform li input.button{
    background: #F5821F;
    color: #fff;
    border-radius: 3px;
    padding: 10px 15px;
    display: inline-block;
    box-shadow: none;
    text-transform: uppercase;
    border: 0;
    height: auto;
    font-weight: 500;
    font-size: 16px;
    margin: 8px 0 0;
    -webkit-transition: all 0.35s ease 0s;
       -moz-transition: all 0.35s ease 0s;
        -ms-transition: all 0.35s ease 0s;
         -o-transition: all 0.35s ease 0s;
            transition: all 0.35s ease 0s;
}
.klick-ats-advanceform li input.button.disabled{
    color: #666!important;
    cursor: no-drop;
    background: #ddd!important;
    text-shadow: none!important;
}
#wpbody select{
    max-width: 100%;
}
#search_by_description{
    vertical-align: middle;
}
#ats_create_db{
    background: #F5821F;
    color: #fff;
    border-radius: 3px;
    padding: 10px 15px;
    display: inline-block;
    box-shadow: none;
    text-transform: uppercase;
    border: 0;
    height: auto;
    font-weight: 500;
    font-size: 16px;
    margin: -9px 0 0;
    -webkit-transition: all 0.35s ease 0s;
       -moz-transition: all 0.35s ease 0s;
        -ms-transition: all 0.35s ease 0s;
         -o-transition: all 0.35s ease 0s;
            transition: all 0.35s ease 0s;
}

/* Status loader */
.downloaded-theme-status{
    width: 98%;
    display: inline-block;
    vertical-align: top;
    border: 1px solid #083D6E;
    border-radius: 3px;
    position: relative;
    /* height: 27px;  */
    display: none;
}
.downloaded-theme-status span{
    position: absolute;
    height: 100%;
    width: 98%;
    border-radius: 3px;
    display: inline-block;
    left: 0;
    top: 0;
    /* background: #F5821F; */
    display: none;

}

